import './style.css'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { gsap } from 'gsap'
import CharacterControls  from './characterControls'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
import { KeyDisplay } from './utils'
import nipplejs from  'nipplejs'
import { Controller } from 'lil-gui'


/**
 * Loader
 */

// Draco loader
const dracoLoader = new DRACOLoader()//loadingManager
dracoLoader.setDecoderPath('/draco/')

// GLTF loader
const gltfLoader = new GLTFLoader()//loadingManager
gltfLoader.setDRACOLoader(dracoLoader)

const loader = new FBXLoader();

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

var geometry = new THREE.BoxGeometry(1,1,1);
var cubeMaterial = new THREE.MeshNormalMaterial(); 

var mesh = new THREE.Mesh( geometry, cubeMaterial );
scene.add( mesh );


/**
 * Model
 */
let mixer = null 
var characterControls
var myModel

gltfLoader.load(
    'Charater.glb',
    (gltf) => {
        const model = gltf.scene;
        model.traverse((child) => {
            child.castShadow = true
        })
        scene.add(model)

        myModel = model

        // model.scale.set(1.5, 1.5, 1.5)
        // model.rotateY(Math.PI)

        const gltfAnimations = gltf.animations;
        const mixer = new THREE.AnimationMixer(model);      

        const animationsMap = new Map();
        gltfAnimations.filter(a => a.name != 'TPose').forEach(function(a) {
            animationsMap.set(a.name, mixer.clipAction(a));
        });

        // var WM = document.getElementById('toggleButton')
        // WM.onclick = () => {
        //     if (WM){
        //         const clip = THREE.AnimationClip.findByName( gltfAnimations, 'Walk' );
        //         const action = mixer.clipAction( clip );
        //         action.play();
        //         console.log(action)
        //     }
        // }

        // Loaded fbx animation
            const fbxLoader = new FBXLoader();
            fbxLoader.load('SalsaDancing.fbx', (animation) => {
                const fbxAnimation = animation.animations[0];
                const fbxAction = mixer.clipAction(fbxAnimation);
                animationsMap.set('animation', fbxAction);
                console.log(fbxAnimation)

                // key listener
                document.addEventListener('keydown', function (event) {
                if (event.code === 'Space') {
                    if(fbxAction.isRunning()) {
                        fbxAction.stop();
                    }else {
                        fbxAction.play();
                    }
                }
            })
        });
        characterControls = new CharacterControls(model, mixer, animationsMap, orbitControls, camera, 'Idle');
    }
)
console.log(myModel)


gltfLoader.load(
    'City.glb',
    (gltf) => {
        const model2 = gltf.scene;
        model2.traverse((child) => {
            child.receiveShadow = true
            // child.castShadow = true

        })

        mixer = new THREE.AnimationMixer(model2)
        const clips = gltf.animations;
        clips.forEach(function(clip) {
            const action = mixer.clipAction(clip);
            action.play();
        })
        // model2.position.y = -0.1
        model2.scale.set(8, 8, 8)
        scene.add(model2)
    }
)
/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () => {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */

const cameraGroup = new THREE.Group()
scene.add(cameraGroup)


// Base camera
let camera = new THREE.PerspectiveCamera(65, sizes.width / sizes.height, 0.1, 10000)
camera.position.set(0, 1, 1.5)
// camera.lookAt(model.position)
scene.add(camera)

/**
 * Lights
 */

const ambientLight = new THREE.AmbientLight(0xffffff, 1.5)
scene.add(ambientLight) 

const directionlLight = new THREE.DirectionalLight('#f3e7d3', 1.5)
directionlLight.position.set(30, 20, 20)
directionlLight.castShadow = true;
directionlLight.shadow.camera.top = 300;
directionlLight.shadow.camera.bottom = - 300;
directionlLight.shadow.camera.left = - 300;
directionlLight.shadow.camera.right = 300;
directionlLight.shadow.camera.near = 10;
directionlLight.shadow.camera.far = 200;
directionlLight.shadow.mapSize.width = 4096;
directionlLight.shadow.mapSize.height = 4096;

const directionlLight2 = new THREE.DirectionalLight('#f3e7d3', 2)
directionlLight2.position.set(-30, -40, -30)
// directionlLight2.castShadow = true;
directionlLight2.shadow.camera.top = 50;
directionlLight2.shadow.camera.bottom = - 50;
directionlLight2.shadow.camera.left = - 50;
directionlLight2.shadow.camera.right = 50;
directionlLight2.shadow.camera.near = 0.1;
directionlLight2.shadow.camera.far = 200;
directionlLight2.shadow.mapSize.width = 4096;
directionlLight2.shadow.mapSize.height = 4096;
scene.add(directionlLight,directionlLight2)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setClearColor('#a8def0')
renderer.shadowMap.enabled = true
renderer.physicallyCorrectLights = true
renderer.outputEncoding = THREE.sRGBEncoding
renderer.toneMapping = THREE.ReinhardToneMapping
renderer.toneMappingExposure = 1.2

// CONTROLS
const orbitControls = new OrbitControls(camera, renderer.domElement);
orbitControls.enableDamping = true
orbitControls.minDistance = 5
orbitControls.maxDistance = 15
orbitControls.enablePan = false
orbitControls.enableZoom = false
orbitControls.maxPolarAngle = Math.PI / 2
orbitControls.minPolarAngle = 0
orbitControls.autoRotate = false
orbitControls.autoRotateSpeed = 0
orbitControls.rotateSpeed = 0.4
// orbitControls.minAzimuthAngle = - Math.PI/2; // radians
// orbitControls.maxAzimuthAngle = Math.PI/4 

orbitControls.update();


// Controls Keys
const keysPressed = {}
// const keyDisplayQueue = new KeyDisplay();
document.addEventListener('keydown', (event) => {
    // keyDisplayQueue.down(event.key)
    
    if(event.shiftKey && characterControls) {
        characterControls.switchRunToggle();
    } else {
        (keysPressed) [event.key.toLowerCase()] = true
    }
}, false);

document.addEventListener('keyup', (event) => {
    // keyDisplayQueue.up(event.key);
    (keysPressed) [event.key.toLowerCase()] = false
}, false);


// joystick
let keyClick = {}
let fwdValue = 0;
let bkdValue = 0;
let rgtValue = 0;
let lftValue = 0;
let tempVector = new THREE.Vector3();
let upVector = new THREE.Vector3(0, 1, 0);
let joyManager;


//  update Player function for joystick
const updatePlayer = () => {
    const angle = orbitControls.getAzimuthalAngle()

    if (fwdValue > 0) {
        tempVector
            .set(0, 0, -fwdValue)
            .applyAxisAngle(upVector, angle)
            myModel.position.addScaledVector(
                tempVector,
                1
            )
            // console.log("forward")
    }

    if (bkdValue > 0) {
        tempVector
          .set(0, 0, bkdValue)
          .applyAxisAngle(upVector, angle)
        myModel.position.addScaledVector(
          tempVector,
          1
        )
    }

    if (lftValue > 0) {
        tempVector
          .set(-lftValue, 0, 0)
          .applyAxisAngle(upVector, angle)
        myModel.position.addScaledVector(
          tempVector,
          1
        )
    }

    if (rgtValue > 0) {
        tempVector
          .set(rgtValue, 0, 0)
          .applyAxisAngle(upVector, angle)
        myModel.position.addScaledVector(
          tempVector,
          1
        )
    }

    // mesh.updateMatrixWorld()
    // camera.position.sub(orbitControls.target)
    // orbitControls.target.copy(mesh.position)
    // camera.position.add(mesh.position)
}

let targetDivF =  document.getElementById('fwd');
let targetDivB =  document.getElementById('bwd');
let targetDivL =  document.getElementById('lwd');
let targetDivR =  document.getElementById('rwd');

// if(/iPhone|iPad|iPod|Android/.test(navigator.userAgent)) {
function addJoystick(){
    let fwdValue = 0;
    let bkdValue = 0;
    let rgtValue = 0;
    let lftValue = 0;

    const options = {
         zone: document.getElementById('joystick'),
         size: 120,
         multitouch: true,
         maxNumberOfNipples: 1,
         mode: 'static',
         restJoystick: true,
         shape: 'circle',
         // position: { top: 20, left: 20 },
         position: { top: '0px', left: '60px' },
         dynamicPage: true,
       }
    
    
        joyManager = nipplejs.create(options);
   
        joyManager['0'].on('move', function (evt, data) {
        //  const forward = (data.vector.y / 4 ) /2
        //  const turn = (data.vector.x / 4 )/2
         const forward = data.vector.y
         const turn = data.vector.x

        targetDivF.innerHTML = 'forward:- ' + fwdValue
        targetDivB.innerHTML = 'backward:- ' + bkdValue
        targetDivL.innerHTML = 'leftside:- ' + lftValue
        targetDivR.innerHTML = 'Rightside:- ' + rgtValue

 
         if (forward > 0.5) {
           fwdValue = Math.abs(forward)
           bkdValue = 0
           keyClick = true
           console.log("forward moved", fwdValue)
         } else if (forward < -0.5) {
           fwdValue = 0
           bkdValue = Math.abs(forward)
           keyClick = true
           console.log("backward moved", bkdValue)
         }
         if (turn > 0.5) {
           lftValue = 0
           rgtValue = Math.abs(turn)
           keyClick = true
           console.log("right moved")
         } else if (turn < -0.) {
           lftValue = Math.abs(turn)
           rgtValue = 0
           keyClick = true
           console.log("left moved")
         }
       })
 
      joyManager['0'].on('end', function (evt) {
         bkdValue = 0
         fwdValue = 0
         lftValue = 0
         rgtValue = 0

         keyClick = false
       })
   
 }

 addJoystick();
// }


/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () => {
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    // Animation Update

    if(mixer !== null)
    {
        mixer.update(deltaTime)
    }

    if (characterControls) {
        characterControls.update(deltaTime, keysPressed, keyClick);
    }

    // Render
    renderer.render(scene, camera)
    orbitControls.update()

    updatePlayer();

    // textureV.needsUpdate = true;

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
    // requestAnimationFrame(tick)
}
tick()





