import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { A, D, DIRECTIONS, DIRECTIONS2, S, W, FW, BW, RW, LW } from './utils'


export default class CharacterControls {

    model;
    mixer;
    animationsMap = new Map(); // Walk, Run, Idle
    orbitControl;
    camera;

    // state
    toggleRun = true;
    currentAction;
    currentAction2;
    

    // temporary data
    walkDirection = new THREE.Vector3();
    rotateAngle = new THREE.Vector3(0, 1, 0);
    rotateQuarternion = new THREE.Quaternion();
    // rotateQuarternion2 = new THREE.Quaternion();
    cameraTarget = new THREE.Vector3();
    
    // constants
    fadeDuration = 0.2;
    runVelocity = 20;
    walkVelocity = 10;

   

    constructor(model,
        mixer, animationsMap,
        orbitControl, camera,
        currentAction, currentAction2) {
        this.model = model;
        this.mixer = mixer;
        this.animationsMap = animationsMap;
        this.currentAction = currentAction;
        this.currentAction2 = currentAction2;
        this.animationsMap.forEach((value, key) => {
            if (key == currentAction) {
                value.play();
            }

            if (key == currentAction2) {
                value.play();
            }
        });
        this.orbitControl = orbitControl;
        this.camera = camera;
        this.updateCameraTarget(0,0);
    }

    switchRunToggle() {
        this.toggleRun = !this.toggleRun;
    }

    
    // Manuplating animation and there state fade
    update(delta, keysPressed , keyClick) {

        const directionPressed = DIRECTIONS.some(key => keysPressed[key] == true);
        const directionPressed2 = keyClick == true

        var play = '';
        if (directionPressed && this.toggleRun) {
            play = 'Run';
            // console.log('run')
        }else if (directionPressed2 && this.toggleRun) {
            play = 'Run';
            // console.log('?')
        }else if (directionPressed) {
            play = 'Walk';
        }else if (directionPressed2) {
            play = 'Walk';
        } else {
            play = 'Idle';
        } 

        if (this.currentAction != play) {
            const toPlay = this.animationsMap.get(play);
            const current = this.animationsMap.get(this.currentAction);

            current.fadeOut(this.fadeDuration);
            toPlay.reset().fadeIn(this.fadeDuration).play();

            this.currentAction = play;
        }

        this.mixer.update(delta);

        // Action troughwirds Camera direction  
        if (this.currentAction == 'Run' || this.currentAction == 'Walk') {
            // calculate towards camera direction
            var angleYCameraDirection = Math.atan2(
                    (this.camera.position.x - this.model.position.x), 
                    (this.camera.position.z - this.model.position.z));
            // diagonal movement angle offset
            var directionOffset = this.directionOffset(keysPressed);
            var directionOffset2 = this.directionOffset2(keyClick)

            // rotate model
            this.rotateQuarternion.setFromAxisAngle(this.rotateAngle, angleYCameraDirection + directionOffset, angleYCameraDirection + directionOffset2);
            this.model.quaternion.rotateTowards(this.rotateQuarternion, 0.2);

            // calculate direction
            this.camera.getWorldDirection(this.walkDirection);
            this.walkDirection.y = 0;
            this.walkDirection.normalize();
            this.walkDirection.applyAxisAngle(this.rotateAngle, directionOffset, directionOffset2);
            // console.log("here's", this.walkDirection)


            // run/walk velocity
            const velocity = this.currentAction == 'Run' ? this.runVelocity : this.walkVelocity;

            // move model & camera with using velocity 
            const moveX = this.walkDirection.x * velocity * delta;
            const moveZ = this.walkDirection.z * velocity * delta;
            this.model.position.x += moveX;
            this.model.position.z += moveZ;
            this.updateCameraTarget(moveX, moveZ);
        }

        
    }

    // Updating the camera follow postion to object using gerWorldDirection
    updateCameraTarget(moveX, moveZ) {
        // move camera
        this.camera.position.x += moveX;
        this.camera.position.z += moveZ;
    
        // update camera target
        this.cameraTarget.x = this.model.position.x;
        this.cameraTarget.y = this.model.position.y + 1;
        this.cameraTarget.z = this.model.position.z;
        this.orbitControl.target = this.cameraTarget;

        
    }
    

    // Calculation for keypress
    directionOffset(keysPressed) {
        var directionOffset = 0; // w
        
        if (keysPressed[W]) {
            if (keysPressed[A]) {
                directionOffset = Math.PI / 4; // w+a
            } else if (keysPressed[D]) {
                directionOffset = - Math.PI / 4; // w+d
            } 
        } else if (keysPressed[S]) {
            if (keysPressed[A]) {
                directionOffset = Math.PI / 4 + Math.PI / 2; // s+a
            } else if (keysPressed[D]) {
                directionOffset = -Math.PI / 4 - Math.PI / 2; // s+d
            } else {
                directionOffset = Math.PI; // s
            }
        } else if (keysPressed[A]) {
            directionOffset = Math.PI / 2; // a
        } else if (keysPressed[D]) {
            directionOffset = - Math.PI / 2; // d
        }
    
        return directionOffset;
    }

    directionOffset2(keyClick) {
        var directionOffset2 = 0;
      
        if (keyClick.direction) {
          const angle = keyClick.angle.radian;
          if (angle >= Math.PI / 4 && angle < Math.PI * 3 / 4) {
            directionOffset2 = Math.PI / 2; // up
          } else if (angle >= Math.PI * 3 / 4 || angle < -Math.PI * 3 / 4) {
            directionOffset2 = Math.PI; // left
          } else if (angle >= -Math.PI * 3 / 4 && angle < -Math.PI / 4) {
            directionOffset2 = -Math.PI / 2; // down
          }
        }
        console.log(directionOffset2, "working")
        return directionOffset2;
      }

}




